import DeriNumberFormat from "../../utils/DeriNumberFormat";
import classNames from "classnames";
import { bg } from "../../utils/utils";
const Tip = (props) => {
  const { underlier,
    active,
    payload,
    onChange,
    lastData,
    isLong } = props
  if (active && payload && payload.length) {
    onChange(payload, active);
    return (
      <div className="tip-chart">
        {/* <div className="hr-chart-tip" style={{top:viewBox.y,left:viewBox.x}}></div> */}
        <div className="price-chart-box">
          <div className="tip-market-price-pnl">
            <div className="tip-market-name"><span className={classNames("cur-index-price-tip", {
              "long-color": isLong,
            })}></span> {bg(lastData.curIndexPrice).eq(payload[0].payload.curIndexPrice) ? `Current ${underlier} Price` : `${underlier} Price`}  </div>
            <div className="tip-market-price">
              <DeriNumberFormat
                value={payload[0].payload.curIndexPrice}
                decimalScale={2}
              />
            </div>
          </div>
        </div>
      </div>
    );
  } else if (lastData.curIndexPrice) {
    let arr = [];
    let payload = {
      payload: lastData,
    };
    arr.push(payload);
    onChange(arr);
    return (null);
  } else {
    let arr = [];
    let payload = {
      payload: lastData,
    };
    arr.push(payload);
    onChange(arr);
    return null;
  }
};

export default Tip;
